.fade-in {
  animation: fadeIn 0.7s;
}

.fade-out {
  animation: fadeOut 0.7s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
*, *:before, *:after {
  box-sizing: border-box;
  user-select: none;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

html {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100vh;
}

body {
  line-height: 1;
  background-color: #ffebde;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

html, body {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
}

ol, ul {
  list-style: none;
}

img {
  pointer-events: none;
}

.hide {
  display: none !important;
}

* {
  letter-spacing: 0.0625rem;
}

.display-1 {
  color: #444444;
  font-family: "Roboto";
  font-size: 2.875rem;
  font-weight: 700;
  margin: 0.625rem 0;
}
@media (max-width: 1199px) {
  .display-1 {
    font-size: 2.75rem;
  }
}
@media (max-width: 1019px) {
  .display-1 {
    font-size: 2.5rem;
  }
}
@media (max-width: 719px) {
  .display-1 {
    text-align: center;
    font-size: 2.375rem;
    margin: 0;
  }
}
@media (max-width: 479px) {
  .display-1 {
    text-align: center;
  }
}

a, .display-3 {
  color: #444444;
  font-family: "Roboto";
  font-size: 0.875rem;
  font-weight: 400;
  letter-spacing: 0;
}
@media (max-width: 719px) {
  a, .display-3 {
    font-size: 0.8125rem;
  }
}
@media (max-width: 479px) {
  a, .display-3 {
    font-size: 0.75rem;
  }
}

a {
  color: #FC917F;
}

button {
  color: #fff;
  font-size: 0.9375rem;
}
@media (max-width: 1019px) {
  button {
    font-size: 0.875rem;
  }
}
@media (max-width: 719px) {
  button {
    font-size: 0.8125rem;
  }
}

.input {
  font-size: 1rem;
  color: #444444;
  font-weight: 400;
}

.input::placeholder {
  color: #444444;
  font-family: "Roboto";
  font-size: 0.875rem;
  font-weight: 400;
  opacity: 0.8;
}
@media (max-width: 1019px) {
  .input::placeholder {
    font-size: 0.8125rem;
  }
}
@media (max-width: 719px) {
  .input::placeholder {
    font-size: 0.75rem;
  }
}

.policy {
  width: 80%;
  padding: 1.25rem 0.625rem;
  text-align: center;
  line-height: 1.5;
}
@media (max-width: 1019px) {
  .policy {
    width: 100%;
    padding: 1.25rem 1.25rem;
  }
}
@media (max-width: 719px) {
  .policy {
    width: 100%;
    padding: 1.25rem 2.5rem;
  }
}
@media (max-width: 479px) {
  .policy {
    width: 100%;
    padding: 1.25rem 0;
  }
}

.form {
  display: flex;
  flex-direction: row;
  width: 74%;
  height: 76%;
  background-color: #fff;
  border-radius: 1.25rem;
  box-shadow: 0px 10px 15px 4px rgba(0, 0, 0, 0.1);
}
@media (max-width: 719px) {
  .form {
    height: 80%;
    height: 86%;
  }
}
@media (max-width: 479px) {
  .form {
    width: 85%;
    height: 94%;
  }
}
.form__signin, .form__signup {
  width: 50%;
  height: 100%;
  padding: 2.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1199px) {
  .form__signin, .form__signup {
    width: 60%;
  }
}
@media (max-width: 1019px) {
  .form__signin, .form__signup {
    width: 70%;
    padding: 2.5rem 3.125rem;
  }
}
@media (max-width: 719px) {
  .form__signin, .form__signup {
    width: 100%;
    padding: 2.5rem 2.875rem;
  }
}
@media (max-width: 479px) {
  .form__signin, .form__signup {
    padding: 2.5rem 1.875rem;
  }
}
.form__signin .form-wrapper, .form__signup .form-wrapper {
  display: block;
  width: 100%;
}
.form__signin .form-wrapper form, .form__signup .form-wrapper form {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2.5rem 0;
}
.form__signin .form-wrapper form .input, .form__signup .form-wrapper form .input {
  outline: none;
  border: none;
  border-bottom: #FC917F solid 0.125rem;
}
.form__signin .form-wrapper form .input, .form__signup .form-wrapper form .input {
  width: 100%;
  height: 2.5rem;
  margin: 0.25rem 0;
}
.form__signin .form-wrapper form .remember-password, .form__signup .form-wrapper form .remember-password {
  width: 100%;
  height: 2.5rem;
  margin: 0.25rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0.625rem 0;
  margin-bottom: 0.625rem;
}
@media (max-width: 359px) {
  .form__signin .form-wrapper form .remember-password, .form__signup .form-wrapper form .remember-password {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
  }
}
.form__signin .form-wrapper form .remember-password .remember-me, .form__signup .form-wrapper form .remember-password .remember-me {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.form__signin .form-wrapper form .remember-password .remember-me .checkbox, .form__signup .form-wrapper form .remember-password .remember-me .checkbox {
  width: 0.9375rem;
  height: 0.9375rem;
  accent-color: #FC917F;
}
.form__signin .form-wrapper form .remember-password .remember-me h3, .form__signup .form-wrapper form .remember-password .remember-me h3 {
  margin-left: 0.25rem;
}
@media (max-width: 359px) {
  .form__signin .form-wrapper form .remember-password .forget-password, .form__signup .form-wrapper form .remember-password .forget-password {
    width: 100%;
    text-align: center;
    margin: 0.625rem 0;
    margin-top: 1.875rem;
  }
}
.form__signin .form-wrapper form .input:-webkit-autofill,
.form__signin .form-wrapper form .input:-webkit-autofill:hover,
.form__signin .form-wrapper form .input:-webkit-autofill:focus,
.form__signin .form-wrapper form .input:-webkit-autofill:active, .form__signup .form-wrapper form .input:-webkit-autofill,
.form__signup .form-wrapper form .input:-webkit-autofill:hover,
.form__signup .form-wrapper form .input:-webkit-autofill:focus,
.form__signup .form-wrapper form .input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #444444 !important;
}
.form__signin .form-wrapper form .input::selection,
.form__signin .form-wrapper form .input:-webkit-autofill::selection,
.form__signin .form-wrapper form .input:-webkit-autofill::selection,
.form__signin .form-wrapper form .input:-webkit-autofill::selection,
.form__signin .form-wrapper form .input:-webkit-autofill:active::selection, .form__signup .form-wrapper form .input::selection,
.form__signup .form-wrapper form .input:-webkit-autofill::selection,
.form__signup .form-wrapper form .input:-webkit-autofill::selection,
.form__signup .form-wrapper form .input:-webkit-autofill::selection,
.form__signup .form-wrapper form .input:-webkit-autofill:active::selection {
  color: #fff;
  background-color: #FC917F;
  -webkit-text-fill-color: #fff !important;
}

.form__img {
  display: block;
  position: relative;
  width: 50%;
  height: 100%;
  border-top-left-radius: 1.25rem;
  border-bottom-left-radius: 1.25rem;
  overflow: hidden;
}
@media (max-width: 1199px) {
  .form__img {
    width: 40%;
  }
}
@media (max-width: 1019px) {
  .form__img {
    width: 30%;
  }
}
@media (max-width: 719px) {
  .form__img {
    display: none;
  }
}
.form__img .cover {
  width: 100%;
  height: 100%;
  padding-right: 1px;
}
.form__img .cover img {
  width: fit-content;
  float: right;
}
.form__img img {
  height: 100%;
}
.form__img .wave {
  position: absolute;
  top: 0;
  right: 0;
}

form button {
  width: 7.5rem;
  height: 2.625rem;
  border: none;
  background-color: #FC917F;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
  margin-top: 1.25rem;
}
form button:hover {
  background-color: #ffa9a1;
}